import { Component } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import {
  DataService,
  pageSelection,
  apiResultFormat,
  routes,
  SidebarService,
} from 'src/app/core/core.index';
import { announcementList } from 'src/app/shared/model/page.model';
import { PaginationService, tablePageSize } from 'src/app/shared/shared.index';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
declare var bootstrap: any;

interface data {
  value: string;
}
@Component({
  selector: 'app-announcement-list',
  templateUrl: './announcement-list.component.html',
  styleUrl: './announcement-list.component.scss',
})
export class AnnouncementListComponent {
  initChecked = false;
  public routes = routes;
  // pagination variables
  public tableData: Array<announcementList> = [];
  public pageSize = 10;
  public serialNumberArray: Array<number> = [];
  public totalData = 0;
  showFilter = false;
  dataSource!: MatTableDataSource<announcementList>;
  public searchDataValue = '';
  //** / pagination variables
  announcementForm!: FormGroup;
  editAnnouncementForm!: FormGroup;

  profileImageBase64: string = ''; // Store the Base64 image
  editProfileImageBase64: string = ''; // Edit Image
editAnnouncementId!: number;
  constructor(
    private data: DataService,
    private pagination: PaginationService,
    private router: Router,
    private sidebar: SidebarService,
    private fb: FormBuilder

  ) {

    this.announcementForm = this.fb.group({
          title: ['', Validators.required],
          description: ['', Validators.required],
          images: [''], // Store Base64 image
        });
         // Edit Form
  this.editAnnouncementForm = this.fb.group({
    title: ['', Validators.required],
    description: ['', Validators.required],
    images: [''],
  });
    this.data.getDataTable().subscribe((apiRes: apiResultFormat) => {
      this.totalData = apiRes.totalData;
      this.pagination.tablePageSize.subscribe((res: tablePageSize) => {
        if (this.router.url == this.routes.announcementList) {
          this.getTableData({ skip: res.skip, limit: this.totalData  });
          this.pageSize = res.pageSize;
        }
      });
    });
  }

  private getTableData(pageOption: pageSelection): void {
    this.data.getAnnouncementList().subscribe((apiRes: apiResultFormat) => {
      console.log(apiRes)
      this.tableData = [];
      this.serialNumberArray = [];
      this.totalData = apiRes.totalData;
      apiRes.data.map((res: announcementList, index: number) => {
        const serialNumber = index + 1;
        if (index >= pageOption.skip && serialNumber <= pageOption.limit) {
          res.sNo = serialNumber;
          this.tableData.push(res);
          this.serialNumberArray.push(serialNumber);
        }
      });
      this.dataSource = new MatTableDataSource<announcementList>(this.tableData);
      this.pagination.calculatePageSize.next({
        totalData: this.totalData,
        pageSize: this.pageSize,
        tableData: this.tableData,
        serialNumberArray: this.serialNumberArray,
      });
    });
  }
  public onFileSelected(event: any, mode: 'create' | 'edit'): void {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        if (mode === 'create') {
          this.profileImageBase64 = reader.result as string;
          this.announcementForm.patchValue({ images: this.profileImageBase64 });
        } else {
          this.editProfileImageBase64 = reader.result as string;
          this.editAnnouncementForm.patchValue({ images: this.editProfileImageBase64 });
        }
      };
      reader.onerror = (error) => {
        console.error('Error converting image to Base64:', error);
      };
    }
  }

  public onSubmit(): void {
    if (this.announcementForm.valid) {
      const updatedObj = [this.announcementForm.value]

      this.data.createAnnouncement(updatedObj).subscribe(
        (response) => {
          console.log('User created successfully', response);
          this.getTableData({ skip: 0, limit: this.totalData });
          // Reset form after submission
          this.announcementForm.reset();
          this.profileImageBase64 = '';
           // Close the modal using Bootstrap's API
           const modalElement = document.getElementById('add-announcement');
           if (modalElement) {
             const modal = bootstrap.Modal.getInstance(modalElement);
             modal.hide();
           }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }

  public editAnnouncement(data: any): void {
    this.editAnnouncementId = data.announcement_id; // Store ID
    this.editAnnouncementForm.patchValue({
      title: data.title,
      description: data.description,
      images: data.images[0]?.announcement_img_url || '',
    });
  
    this.editProfileImageBase64 = data.images[0]?.announcement_img_url || ''; // Show existing image
  }
  public deleteAnnouncement(data: any): void {
    this.editAnnouncementId = data.announcement_id; // Store ID
    Swal.fire({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#d33',
          cancelButtonColor: '#3085d6',
          confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
          if (result.isConfirmed) {
            this.data.deleteAnnouncement(this.editAnnouncementId).subscribe(
              response => {
                Swal.fire('Deleted!', 'User has been deleted.', 'success');
                this.getTableData({ skip: 0, limit: this.totalData });
              },
              error => {
                Swal.fire('Error!', 'Failed to delete user.', 'error');
              }
            );
          }
        });
  }

 public onUpdate(): void {
  if (this.editAnnouncementForm.valid) {
    const formData = { ...this.editAnnouncementForm.value };
    this.data.updateAnnouncement(this.editAnnouncementId, formData).subscribe(
      (response) => {
        console.log('Announcement updated successfully', response);
        this.getTableData({ skip: 0, limit: this.totalData });
        const modalElement = document.getElementById('edit-announcement');
        if (modalElement) {
          const modal = bootstrap.Modal.getInstance(modalElement);
          modal.hide();
        }
        // this.resetEditForm();
      },
      (error) => {
        console.error('Error updating announcement', error);
      }
    );
  }
}

  public sortData(sort: Sort) {
    const data = this.tableData.slice();
    if (!sort.active || sort.direction === '') {
      this.tableData = data;
    } else {
      this.tableData = data.sort((a, b) => {
        const aValue = (a as never)[sort.active];
        const bValue = (b as never)[sort.active];
        return (aValue < bValue ? -1 : 1) * (sort.direction === 'asc' ? 1 : -1);
      });
    }
  }

  public searchData(value: string): void {
    this.dataSource.filter = value.trim().toLowerCase();
    this.tableData = this.dataSource.filteredData;
  }

  confirmColor() {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: ' btn btn-success',
        cancelButton: 'me-2 btn btn-danger',
      },
      buttonsStyling: false,
    });

    swalWithBootstrapButtons
      .fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        confirmButtonText: 'Yes, delete it!',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          );
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          );
        }
      });
  }

  public selectedValue1 = '';
  public selectedValue2 = '';
  public selectedValue3 = '';

  selectedList1: data[] = [
    { value: 'Sort by Date' },
    { value: 'Newest' },
    { value: 'Oldest' },
  ];
  selectedList2: data[] = [
    { value: 'Choose Brand' },
    { value: 'Lenevo' },
    { value: 'Boat' },
    { value: 'Nike' },
  ];
  selectedList3: data[] = [
    { value: 'Choose Status' },
    { value: 'Active' },
    { value: 'Inactive' },
  ];

  public filter = false;
  openFilter() {
    this.filter = !this.filter;
  }
  isCollapsed: boolean = false;
  toggleCollapse() {
    this.sidebar.toggleCollapse();
    this.isCollapsed = !this.isCollapsed;
  }
  selectAll(initChecked: boolean) {
    if (!initChecked) {
      this.tableData.forEach((f) => {
        f.isSelected = true;
      });
    } else {
      this.tableData.forEach((f) => {
        f.isSelected = false;
      });
    }
  }
}
