import { Component,ElementRef, ViewChild } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import {
  DataService,
  pageSelection,
  apiResultFormat,
  routes,
  SidebarService,
} from 'src/app/core/core.index';
import { categoryList } from 'src/app/shared/model/page.model';
import { PaginationService, tablePageSize } from 'src/app/shared/shared.index';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

interface data {
  value: string;
}
declare var bootstrap: any; // Import Bootstrap globally

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrl: './category-list.component.scss',
})
export class CategoryListComponent {
  @ViewChild('addCategoryModal') addCategoryModal!: ElementRef; // Reference to modal

  initChecked = false;
  public routes = routes;
  public selectedValue1 = '';
  public selectedValue2 = '';
  public selectedValue3 = '';

  // pagination variables
  public tableData: Array<categoryList> = [];
  public pageSize = 10;
  public serialNumberArray: Array<number> = [];
  public totalData = 0;
  showFilter = false;
  dataSource!: MatTableDataSource<categoryList>;
  public searchDataValue = '';
  //** / pagination variables
  categoryForm!: FormGroup;
  editCategoryForm!: FormGroup;
  editCategoryId!: string;
  constructor(
    private data: DataService,
    private pagination: PaginationService,
    private router: Router,
    private sidebar: SidebarService,
    private fb: FormBuilder,
  ) {
    this.data.getDataTable().subscribe((apiRes: apiResultFormat) => {
      this.totalData = apiRes.totalData;
      this.pagination.tablePageSize.subscribe((res: tablePageSize) => {
        if (this.router.url == this.routes.categoryList) {
          this.getTableData({ skip: res.skip, limit: this.totalData  });
          this.pageSize = res.pageSize;
        }
      });
    });

    this.categoryForm = this.fb.group({
      name: ['', Validators.required]
    });
    this.editCategoryForm = this.fb.group({
      name: ['', Validators.required],
      is_active: [true]
    });

  }
  public onSubmit(): void {
    if (this.categoryForm.valid) {
      const updatedObj = { name: [this.categoryForm.value.name] }
      this.data.createCategory(updatedObj).subscribe(
        response => {
          Swal.fire('Success!', 'Category created successfully.', 'success');

          // Reset form
          this.categoryForm.reset();
          this.categoryForm.patchValue({ status: true });

          // Close the modal
          const modalElement = document.getElementById('add-category');
          if (modalElement) {
            const modal = bootstrap.Modal.getInstance(modalElement);
            modal.hide();
          }
          this.data.getDataTable().subscribe((apiRes: apiResultFormat) => {
            this.totalData = apiRes.totalData;
            this.pagination.tablePageSize.subscribe((res: tablePageSize) => {
              if (this.router.url == this.routes.categoryList) {
                this.getTableData({ skip: res.skip, limit: this.totalData  });
                this.pageSize = res.pageSize;
              }
            });
          });
        },
        error => {
          Swal.fire('Error!', 'Failed to create category.', 'error');
        }
      );
    }
  }
  public editcategory(data: any): void {
    this.editCategoryId = data.category_id; // Store ID
    console.log(data)
    this.editCategoryForm.patchValue({
      name: data.name,
      is_active: data.is_active,
    });
    }
    public onUpdate(): void {
      if (this.editCategoryForm.valid) {
        const formData = { ...this.editCategoryForm.value };
        this.data.updateCategory(this.editCategoryId, formData).subscribe(
          (response) => {
            this.getTableData({ skip: 0, limit: this.totalData });
            const modalElement = document.getElementById('edit-category');
            if (modalElement) {
              const modal = bootstrap.Modal.getInstance(modalElement);
              modal.hide();
            }
            // this.resetEditForm();
          },
          (error) => {
            console.error('Error updating announcement', error);
          }
        );
      }
    }

  private getTableData(pageOption: pageSelection): void {
    this.data.getCategoryList().subscribe((apiRes: apiResultFormat) => {
      console.log(apiRes)
      this.tableData = [];
      this.serialNumberArray = [];
      this.totalData = apiRes.totalData;
      apiRes.data.map((res: categoryList, index: number) => {
        const serialNumber = index + 1;
        if (index >= pageOption.skip && serialNumber <= pageOption.limit) {
          res.sNo = serialNumber;
          this.tableData.push(res);
          this.serialNumberArray.push(serialNumber);
        }
      });
      this.dataSource = new MatTableDataSource<categoryList>(this.tableData);
      this.pagination.calculatePageSize.next({
        totalData: this.totalData,
        pageSize: this.pageSize,
        tableData: this.tableData,
        serialNumberArray: this.serialNumberArray,
      });
    });
  }

  public sortData(sort: Sort) {
    const data = this.tableData.slice();
    if (!sort.active || sort.direction === '') {
      this.tableData = data;
    } else {
      this.tableData = data.sort((a, b) => {
        const aValue = (a as never)[sort.active];
        const bValue = (b as never)[sort.active];
        return (aValue < bValue ? -1 : 1) * (sort.direction === 'asc' ? 1 : -1);
      });
    }
  }

  public searchData(value: string): void {
    this.dataSource.filter = value.trim().toLowerCase();
    this.tableData = this.dataSource.filteredData;
  }
  selectedList1: data[] = [
    { value: 'Sort by Date' },
    { value: 'Newest' },
    { value: 'Oldest' },
  ];
  public filter = false;
  openFilter() {
    this.filter = !this.filter;
  }
  isCollapsed: boolean = false;
  toggleCollapse() {
    this.sidebar.toggleCollapse();
    this.isCollapsed = !this.isCollapsed;
  }
  confirmColor() {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: ' btn btn-success',
        cancelButton: 'me-2 btn btn-danger',
      },
      buttonsStyling: false,
    });

    swalWithBootstrapButtons
      .fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        confirmButtonText: 'Yes, delete it!',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          );
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          );
        }
      });
  }
  selectedList2: data[] = [
    { value: 'Choose Category' },
    { value: 'Laptop' },
    { value: 'Electronics' },
  ];
  selectedList3: data[] = [
    { value: 'Choose Status' },
    { value: 'Active' },
    { value: 'Inactive' },
   
  ];
  selectAll(initChecked: boolean) {
    if (!initChecked) {
      this.tableData.forEach((f) => {
        f.isSelected = true;
      });
    } else {
      this.tableData.forEach((f) => {
        f.isSelected = false;
      });
    }
  }
}
